a {
  text-decoration: none !important;
  color: inherit !important;
}

.mainpg {
  display: flex;
}

.pd_pg {
  padding-top: 20px;
  margin-left: 10px;
}

.filterOPtion {
  color: #212529;
  max-width: 50px;
  height: 100vh;
  text-align: left;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
}

.filterbtn-1 {
  color: #212529;
}
.filterbtn-2 {
  color: #ffffff;
}

.openMenu {
  min-width: 25%;
  background-color: #212529;
  max-height: 100%;
  text-align: left;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
}

.filter-Con-0 {
  padding: 20px;
  opacity: 0;
  padding-left: -0px;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
}

.mainpg {
  display: flex;
}

.filter-Con-1 {
  opacity: 1;
  color: white;
  padding: 20px;
  overflow: hidden;
  transition: all 0.6s ease-in-out;
}

.filter-ad {
  display: flex;
  flex-direction: column;
}

.status-checkbox,
.ofs {
  overflow: hidden;
}

.pd_pg {
  display: flex;
  flex-wrap: wrap;
}

.cardText {
  margin: 0px;
  font-size: 16px;
  line-height: 20px;
}

.cardTitle {
  font-size: 16px;
  font-weight: bold;
}

.cardRating {
  float: right;
}

.cardBody {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 5px;
}
.cardbtn {
  margin-right: 10px;
  width: 50px;
}
.cartQty {
  background-color: transparent;
}

.cartList-con {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.cartProd {
  flex: 65%;
}
.RemoveFromCart {
  flex: 15%;
}

.cartProd h4,
p {
  margin-bottom: 0;
  font-size: 13px;
}

.cartProd h4 {
  font-size: 15px;
  margin-right: 15px;
  margin-bottom: 0;
}

.cartImg {
  width: 35px;
  overflow: hidden;
  border: 1px solid rgb(231, 222, 222);
  flex: 20%;
  margin-right: 15px;
}

.Cart-total {
  margin: 0;
  background-color: #212529;
  width: 100%;
  height: 90vh;
  color: white;
  padding: 20px;
}

.Cart-total-heading {
  color: white;
}

.cartContainer{
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.cartImage{
 
  overflow: hidden;
  border: 1px solid rgb(231, 222, 222);
 flex: 20%;
  margin-right: 15px;
}

.cartProdName{
  
  flex: 70%;
}

.cartProdName h4{
  font-size: 20px;
  margin-right: 10px;
  margin-bottom: 0;
}

.RemoveCartItem{
  flex: 10%;
}